@import '/src/Styles/abstracts/mixins';
@import '/src/Styles/abstracts/variables';

header {

    @include flexSpaceEven;
    height: $headerHeight;
    @include themeTransition;
    background-color: $light-base;
    padding: 10px;

    #reactImageHeader {
        font-size: 4em;
        position: relative;
        top: 6px;
    }

    h1 {
        position: relative;
        right: 5.4em;
        font-weight: 700;
        border-bottom: 6px double $light-complement;
    }

    nav {
        
        width: 500px;
        font-weight: 300;
        border-radius: 20px;
        @include themeTransition;
        background-color: $light-complement;
        color: $light-base;

        .navMenu {
            @include flexSpaceEven;

            li {
                font-size: 20px;

                a {
                    color: $light-base;
                    @include themeTransition;
                }
            }
            
            
        }
    }

    &.dark {
        background-color: $dark-base;
        color: $dark-complement;

        h1 {
            border-bottom: 6px double $dark-complement;
        }

        nav {
            background-color: $dark-complement;

            li {
                a {
                    color: $dark-base;
                }
            }
        }
    }

}

